import { useState, useRef, useImperativeHandle, forwardRef } from "react";
import pinyin from "pinyin";
import { nanoid } from "nanoid";
import { toast } from "react-toastify";
import { Db } from "../../../database/Db_new";

import FormStyles from "./Form.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faXmark, faPaperPlane } from "@fortawesome/free-solid-svg-icons";

const FormSubmit = (props, e, ref) => {
	e.preventDefault();
	const formData = new FormData(e.target);
	e.target.reset();
	const data = Array.from(formData.entries()).reduce(
		(memo, [key, value]) => ({
			...memo,
			[key]: value,
		}),
		{}
	);
	// chỉ thêm data khi trường đầu tiên hợp lệ
	if (
		[...data.hanzi].filter((char) => /[\u4e00-\u9fa5]/g.test(char)).length >
		0
	)
		Db.transaction("rw", Db.datas, async function () {
			await Db.datas
				.add({
					_id: nanoid(5),
					tag_id: props.tag._id,
					...data,
					is_a_learned_word: false,
					view_count: 0,
					createdAt: new Date(),
				})
				.then(() => {
					ref.current.focus();
					toast.success("Add data sucessfully!", {
						position: toast.POSITION.BOTTOM_LEFT,
					});
				});
		}).catch(console.error);
	else {
		ref.current.focus();
		toast.error("Faild to add data!", {
			position: toast.POSITION.BOTTOM_LEFT,
		});
	}
};

function Form(props, ref) {
	const [isVisible, FormVisibility] = useState(true);
	const hanziRef = useRef();
	const pinyinRef = useRef();
	useImperativeHandle(ref, () => {
		return {
			toggleForm: function () {
				FormVisibility((prev) => !prev);
			},
		};
	});
	return (
		isVisible && (
			<div id='form-wrapper' className={FormStyles.wrapper}>
				<div className={FormStyles["form-header"]}>
					<span>{props.tag.tag_name}</span>
					<button
						className={FormStyles["close-form"]}
						onClick={() => {
							props.setToggleMode((prev) => {
								if (prev === "viewer") return "addData";
								if (prev === "addData") return "viewer";
							});
						}}
					>
						<FontAwesomeIcon icon={faXmark} />
					</button>
				</div>
				<form
					onReset={(e) => {
						e.target.reset();
						hanziRef.current.focus();
					}}
					onSubmit={(e) => FormSubmit(props, e, hanziRef)}
				>
					<div className={FormStyles["Field-group"]}>
						<div className={FormStyles["item"]}>
							<div className={FormStyles["label"]}>hanzi</div>
							<input
								tabIndex={1}
								className={FormStyles["input"]}
								name='hanzi'
								ref={hanziRef}
								onBlur={(e) => {
									let data = e.target.value;
									let pinyinConfig =
										data.length > 2
											? {
													segment: "segmentit",
													group: true,
											  }
											: { heteronym: true };
									pinyinRef.current.value = pinyin(
										data,
										pinyinConfig
									).join(" , ");
								}}
							/>
						</div>
						<div className={FormStyles["item"]}>
							<div className={FormStyles["label"]}>Pinyin</div>
							<input
								tabIndex={-1}
								className={FormStyles["input"]}
								ref={pinyinRef}
								name='pinyin'
							/>
						</div>
					</div>
					<div className={FormStyles["Field-group"]}>
						<div className={FormStyles["item"]}>
							<div className={FormStyles["label"]}>Han Viet</div>
							<input
								tabIndex={2}
								className={FormStyles["input"]}
								name='han_viet'
							/>{" "}
						</div>
						<div className={FormStyles["item"]}>
							<div className={FormStyles["label"]}>
								Vietnamese
							</div>
							<input
								tabIndex={2}
								className={FormStyles["input"]}
								name='Vietnamese_meaning'
							/>{" "}
						</div>
					</div>

					<div className={FormStyles["label"]}>note</div>
					<textarea
						className={FormStyles["input"]}
						spellCheck={false}
						name='note'
						tabIndex={3}
					></textarea>
					<div className={FormStyles["form-btn-group"]}>
						<button
							type='submit'
							tabIndex={4}
							className={FormStyles["btnSubmit"]}
						>
							<FontAwesomeIcon icon={faPaperPlane} />
						</button>
						<button
							type='reset'
							tabIndex={5}
							className={FormStyles["btnReset"]}
						>
							<FontAwesomeIcon icon={faXmark} />
						</button>
					</div>
				</form>
			</div>
		)
	);
}
export default forwardRef(Form);
